/** Testimonial section **/

.Testimonial {
  background: url(../public/images/test_bg.jpg);
  background-repeat: no-repeat;
  padding: 60px 0px;
  background-position: center;
  background-size: 100% 100%;
}
.Testimonial .titlepage {
  text-align: left;
  padding-bottom: 15px;
}
.Testimonial .titlepage h2 {
  color: #fff;
  margin-bottom: 0px;
  padding-bottom: 0px;
}

.Testimonial .titlepage span {
  color: #fff;
}

.Testimonial_box i {
  border: #e6e1e1 solid 10px;
  width: 220px;
  height: 220px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 auto;
  border-radius: 210px;
  cursor: pointer;
  background: #6ad0bc;
  padding: 8px;
}

.Testimonial_box i::after {
  position: absolute;
  content: "";
  width: 50px;
  height: 50px;
  top: -16px;
  left: 34px;
  display: inline-block;
  border: #ddd solid 6px;
  border-radius: 30px;
  background: #6ad0bc;
}

.Testimonial_box {
  text-align: left;
}

.Testimonial_box h4 {
  color: #fff;
  text-transform: uppercase;
  font-size: 20px;
  line-height: 20px;
  font-weight: 400;
  padding: 0;
}

.Testimonial_box p {
  color: #fff;
  font-size: 17px;
  line-height: 28px;
  padding: 25px 0 0px 0;
}

/** end Testimonial section **/

/** contact section **/

.padd_leri0 {
  padding-left: 0;
  padding-right: 0;
}

.contact .titlepage span {
  font-size: 17px;
  line-height: 28px;
}

.contact {
  padding-top: 90px;
}

.main_form {
  padding: 50px 50px;
  border: #046c58 solid 1px;
  margin: 0px 80px;
}

.contact .main_form .form_contril {
  border: #046c58 solid 1px;
  margin-bottom: 20px;
  width: 100%;
  height: 45px;
  background: #fff;
  color: #046c58;
  font-size: 17px;
  border-top: 0;
  border-right: 0;
  border-left: 0;
}

.contact .main_form .textarea {
  border: #046c58 solid 1px;
  margin-bottom: 20px;
  width: 100%;
  height: 60px;
  background: #fff;
  color: #046c58;
  font-size: 17px;
  border-top: 0;
  border-right: 0;
  border-left: 0;
}

.contact .main_form .send_btn {
  font-size: 17px;
  transition: ease-in all 0.5s;
  background-color: #000;
  text-transform: uppercase;
  color: #fff;
  padding: 13px 0px;
  float: right;
  display: block;
  margin-top: 30px;
  width: 100%;
  max-width: 190px;
}

.contact .main_form .send_btn:hover {
  background-color: #046c58;
  transition: ease-in all 0.5s;
  color: #fff;
  border-radius: 26px;
}

#contact *::placeholder {
  color: #046c58;
  opacity: 1;
}

.current {
  color: #fff;
  margin-top: -4px;
  position: absolute;
}

.con_img figure {
  margin: 0;
}

.con_img figure img {
  width: 100%;
}

/** end contact section **/

.read_more {
  font-size: 17px;
  background-color: #fff;
  color: #000;
  padding: 13px 0px;
  width: 100%;
  max-width: 190px;
  text-align: center;
  display: inline-block;
  transition: ease-in all 0.5s;
  border-radius: 10px;
}

.read_more:hover {
  background: #000;
  color: #fff;
  border-radius: 26px;
  transition: ease-in all 0.5s;
}

